import React from 'react'
import "./body.css"
import { AiFillCaretLeft } from "react-icons/ai"
function Body() {
    return (
        <div className="body-container">
            <div className="body-top">

            </div>
            <div className="body-bottom-container">
                <div className="bodyBottomTop">
                    <div className="bodytopcontainer">
                        <AiFillCaretLeft />
                        <p className="bodytop-text">
                            Videos
                        </p>
                    </div>
                </div>
                <div className="bodyBottom">
                    <div className="body-input">
                        <input  placeholder="Insert URL here"></input>
                    </div>

                    <p className="body-text"> or </p>
                    <div className="body-card">upload</div>
                </div>


            </div>
        </div>
    )
}

export default Body
