import React from 'react'
import "./sidebar.css"
import { BiBook } from "react-icons/bi";
import { BsPerson } from "react-icons/bs";
import { AiOutlineSetting } from "react-icons/ai";
import { NavLink } from 'react-router-dom';

function Sidebar() {
    return (
        <div className="sidebar-container">
            <div className="sidebarLogoHolder">
                <img src="./images/logo.png" alt="logo" />

            </div>
            <div className="nav-list">
                <NavLink className="list" activeClassName="active" to="/students">
         
                        <div>
                            <div className="navlogoholder"> <BsPerson size={30} /></div>

                            <p className="listName">Students</p>
                        </div>


                   
                </NavLink>
                <NavLink exact className="list" activeClassName="active" to="/">
               
                        <div>
                            <div className="navlogoholder"> <BiBook size={30} /></div>

                            <p className="listName">Lesson Plan</p>
                        </div>

                  
                </NavLink>
                <NavLink className="list" activeClassName="active" to="/settings">
                   
                        <div>
                            <div className="navlogoholder"> <AiOutlineSetting size={30} /></div>

                            <p className="listName">Settings</p>
                        </div>
                   
                </NavLink>


            </div>

        </div>
    )
}

export default Sidebar
