import './App.css'
import LessonPlan from './pages/LessonPlan'
import {HashRouter as Router ,Switch,Route} from 'react-router-dom'
import Students from './pages/Students'
import Settings from './pages/Settings'
function App() {
    return (
      <Router>
          <Switch>
          <Route path="/" exact>
              <LessonPlan/>
          </Route>
          <Route path="/students">
              <Students/>
          </Route>
          <Route path="/settings" >
              <Settings/>
          </Route>
             

          </Switch>
      </Router>
    )
}

export default App
